import { layoutContentHeight, layoutWidth } from 'style/const/layout';

export const catalogCardWidth = 392;
export const catalogCardHeight = 592;
export const catalogCardBorderRadius = 40;

export const catalogCardGap = 32;

export const fullCardsInViewPort = Math.floor((layoutWidth + catalogCardGap) / (catalogCardWidth + catalogCardGap));

export const catalogContentPaddingY = (layoutContentHeight - catalogCardHeight) / 2;
