import React from 'react';
import styled, { css } from 'styled-components';
import { useSelector } from 'hooks/useSelector';
import { useDispatch } from 'hooks/useDispatch';
import { removeItemAction, addItemAction } from 'store/actions/native';
import { ButtonPortal, ButtonViews } from 'components/Button/Button@portal';
import { IndexBadge } from 'components/IndexBadge/IndexBadge';
import { Label, LabelProps } from 'components/Label/Label';
import { BodyText1, BodyText2 } from 'components/Text/Body';
import { FootnoteText1 } from 'components/Text/Footnote';
import { icon } from 'style/const/icon';
import { color } from 'style/const/color';
import { transition } from 'style/const/transition';
import addIcon from 'assets/icons/add.svg';
import removeIcon from 'assets/icons/remove.svg';

const thumbSizePx = 120;

interface StyledThumbProps {
    imageUrl: string;
}

const StyledThumb = styled.div<StyledThumbProps>`
    position: relative;

    margin-right: 32px;
    flex-shrink: 0;

    height: ${thumbSizePx}px;
    width: ${thumbSizePx}px;

    border-radius: 24px;

    background-image: url('${({ imageUrl }) => imageUrl}');
    background-color: rgba(255, 255, 255, .1);
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
`;

const StyledInfoContainer = styled.div`
    display: flex;
    flex-direction: column;
`;

const StyledInfo = styled.div`
    height: 120px;
    max-width: 360px;
    display: flex;
    flex-direction: column;
    justify-content: center;
`;

interface StyledNameProps {
    isOneLine?: boolean;
}
const StyledName = styled(BodyText1)<StyledNameProps>`
    overflow: hidden;

    ${({ isOneLine }) =>
        isOneLine
            ? css`
                  white-space: nowrap;
                  text-overflow: ellipsis;
              `
            : css`
                  display: -webkit-box;
                  -webkit-box-orient: vertical;
                  -webkit-line-clamp: 2;
              `}

    color: ${color.text.primary};
`;

const StyledPriceContainer = styled.div`
    margin-top: 4px;
    display: flex;
`;

const StyledPrice = styled(FootnoteText1)`
    color: ${color.text.secondary};
`;

const StyledOldPrice = styled(FootnoteText1)`
    position: relative;
    opacity: 0.24;
    color: ${color.text.primary};
    margin-left: 8px;

    &::before {
        content: '';
        position: absolute;
        top: calc(50% - 2px);
        border-top: 4px solid ${color.text.primary};
        left: 2px;
        right: 2px;
    }
`;

const StyledLabel = styled(Label)`
    margin-top: 4px;
`;

const StyledSelector = styled.div`
    margin-top: 12px;

    width: 200px;

    display: flex;
    justify-content: space-between;
    align-items: center;
`;

interface StyledSelectorValue {
    isWarning?: boolean;
}
const StyledSelectorValue = styled(BodyText2)<StyledSelectorValue>`
    color: ${({ isWarning }) => (isWarning ? color.text.warning : color.text.primary)};
`;

type SelectorButtonIconType = 'less' | 'more' | 'remove';

const iconTypeIconMap: Record<SelectorButtonIconType, string> = {
    less: removeIcon,
    more: addIcon,
    remove: addIcon,
};

const selectorButtonSizePx = 64;
const selectorBorderRadiusPx = selectorButtonSizePx / 2;
const selectorIconSizePx = icon.s.sizePx;

interface StyledSelectorButtonProps {
    iconType: SelectorButtonIconType;
}
const StyledSelectorButton = styled.button<StyledSelectorButtonProps>`
    position: relative;

    height: ${selectorButtonSizePx}px;
    width: ${selectorButtonSizePx}px;
    border: none;
    border-radius: ${selectorBorderRadiusPx}px;

    display: flex;
    align-items: center;
    justify-content: center;

    background: ${color.button.secondary1};

    will-change: background-color, transform, opacity;
    transition: ${transition.default};

    &::before {
        content: '';

        height: ${selectorIconSizePx}px;
        width: ${selectorIconSizePx}px;

        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
    }

    &[disabled] {
        opacity: 0.3;
    }

    &::before {
        background-image: url('${({ iconType }) => iconTypeIconMap[iconType]}');
    }

    ${({ iconType }) =>
        iconType === 'remove' &&
        css`
            transform: rotate(45deg);
            background: ${color.button.critical};
        `}
`;

interface StyledCartListItemProps {
    disabled?: boolean;
}

const StyledCartListItem = styled.div<StyledCartListItemProps>`
    position: relative;

    height: 100%;
    width: 100%;

    box-sizing: border-box;

    display: flex;
    align-items: start;

    ${({ disabled }) =>
        disabled &&
        css`
            ${StyledThumb},
            ${StyledName},
            ${StyledPrice} {
                opacity: .24;
            }

            ${StyledPrice} {
                color: ${color.text.primary};
            }
        `}
`;

const StyledIndexBadge = styled(IndexBadge)`
    top: 4px;
    right: 4px;
`;

const StyledRemoveButton = styled(ButtonPortal)`
    margin-top: 12px;
`;

export interface CartListItemPortalProps {
    id: string;
    index: number;
}

export interface CartListItemPortalViewProps {
    imageUrl: string;
    name: string;
    price: number;
    oldPrice?: number;
    count: number;
    isWantMoreThanHave?: boolean;
    disabled?: boolean;
    label?: LabelProps;
    isMoreButtonDisabled?: boolean;
}

export const CartListItemPortal: React.FC<CartListItemPortalProps> = ({ index, id }) => {
    const {
        imageUrl,
        name,
        price,
        oldPrice,
        label,
        count,
        isWantMoreThanHave,
        disabled,
        isMoreButtonDisabled,
    } = useSelector(({ cartPagePortal: { itemsMap } }) => itemsMap[id]);
    const dispatch = useDispatch();

    const handleLessClick = React.useCallback(() => {
        dispatch(
            removeItemAction({
                item: id,
                count: '1',
                shouldRemoveMention: !count,
            }),
        );
    }, [dispatch, id, count]);

    const handleMoreClick = React.useCallback(() => {
        dispatch(
            addItemAction({
                item: id,
                count: '1',
            }),
        );
    }, [dispatch, id]);

    const handleRemoveClick = React.useCallback(() => {
        dispatch(
            removeItemAction({
                item: id,
                count: String(Infinity),
                shouldRemoveMention: true,
            }),
        );
    }, [dispatch, id]);

    return (
        <StyledCartListItem disabled={disabled}>
            <StyledThumb imageUrl={imageUrl}>
                <StyledIndexBadge size="s" index={index} />
            </StyledThumb>
            <StyledInfoContainer>
                <StyledInfo>
                    <StyledName isOneLine={disabled || Boolean(label)}>{name}</StyledName>
                    <StyledPriceContainer>
                        <StyledPrice>{price} </StyledPrice>
                        {oldPrice ? <StyledOldPrice>{oldPrice} </StyledOldPrice> : null}
                    </StyledPriceContainer>
                    {label ? <StyledLabel {...label} /> : null}
                </StyledInfo>
                {disabled ? (
                    <StyledRemoveButton view={ButtonViews.secondary} onClick={handleRemoveClick}>
                        
                    </StyledRemoveButton>
                ) : (
                    <StyledSelector>
                        <StyledSelectorButton
                            type="button"
                            aria-label="less"
                            iconType={count ? 'less' : 'remove'}
                            onClick={handleLessClick}
                        />
                        <StyledSelectorValue isWarning={isWantMoreThanHave}>{count}</StyledSelectorValue>
                        <StyledSelectorButton
                            type="button"
                            aria-label="more"
                            iconType="more"
                            disabled={isMoreButtonDisabled}
                            onClick={handleMoreClick}
                        />
                    </StyledSelector>
                )}
            </StyledInfoContainer>
        </StyledCartListItem>
    );
};
