import { AppReducer, AppState } from 'store/constants';

export const twoGisSuggestsData: AppReducer = (state, action) => {
    switch (action.type) {
        case 'SET_TWO_GIS_SUGGESTS': {
            type TwoGisSuggestsItemsMap = AppState['twoGisSuggestsData']['itemsMap'];

            state.twoGisSuggestsData.itemsMap = action.payload.reduce<TwoGisSuggestsItemsMap>((result, item) => {
                result[item.id] = item;

                return result;
            }, {});

            break;
        }

        default: {
            break;
        }
    }
};
