import { produce } from 'immer';
import { History } from 'history';
import { isMobile, isPortal } from 'tools/browser';
import { initialState, AppBaseAction } from 'store/constants';
import { productsData } from 'store/reducers/data/products';
import { productsDiffData } from 'store/reducers/data/productsDiff';
import { twoGisSuggestsData } from 'store/reducers/data/twoGisSuggests';
import { userData } from 'store/reducers/data/user';
import { deliveryData } from 'store/reducers/data/delivery';
import { getRouterModel } from 'store/reducers/model/router';
import { cartModel } from 'store/reducers/model/cart';
import { userModel } from 'store/reducers/model/user';
import { catalogPageView } from 'store/reducers/view/catalogPage';
import { catalogPageTouchView } from 'store/reducers/view/catalogPage@touch';
import { catalogPagePortalView } from 'store/reducers/view/catalogPage@portal';
import { cartPageView } from 'store/reducers/view/cartPage';
import { cartPageTouchView } from 'store/reducers/view/cartPage@touch';
import { cartPagePortalView } from 'store/reducers/view/cartPage@portal';
import { addressSimplePageView } from 'store/reducers/view/addressSimplePage';
import { addressSimplePagePortalView } from 'store/reducers/view/addressSimplePage@portal';
import { addressFullPageView } from 'store/reducers/view/addressFullPage';
import { addressFullPagePortalView } from 'store/reducers/view/addressFullPage@portal';
import { headerView } from 'store/reducers/view/header';
import { errorPageView } from 'store/reducers/view/errorPage';
import { waitPageView } from 'store/reducers/view/waitPage';

export const getRootReducer = (history: History) => {
    const routerModel = getRouterModel(history);

    const reducers = [
        //  
        productsData,
        productsDiffData,
        twoGisSuggestsData,
        userData,
        deliveryData,

        //   
        routerModel,
        cartModel,
        userModel,
    ];

    //      
    if (isMobile) {
        //   
        reducers.push(
            // prettier-ignore
            cartPageTouchView,
            catalogPageTouchView,
            headerView,
        );
    } else if (isPortal) {
        //   
        reducers.push(
            // prettier-ignore
            catalogPagePortalView,
            cartPagePortalView,
            addressSimplePagePortalView,
            addressFullPagePortalView,
            waitPageView,
            errorPageView,
            headerView,
        );
    } else {
        //    ()
        reducers.push(
            catalogPageView,
            cartPageView,
            addressSimplePageView,
            addressFullPageView,
            waitPageView,
            errorPageView,
            headerView,
        );
    }

    return (state = initialState, action: AppBaseAction) =>
        produce(state, (draft) => {
            reducers.forEach((reducer) => reducer(draft, action));
        });
};
