import React from 'react';
import { useSelector } from 'hooks/useSelector';
import { CatalogListPortal } from 'pages/Catalog/List/CatalogList@portal';
import { CatalogSkeletonPortal } from 'pages/Catalog/Skeleton/CatalogSkeleton@portal';

export const CatalogPortal: React.FC = () => {
    const itemsCount = useSelector(({ catalogPagePortal }) => catalogPagePortal.order.length);

    return itemsCount ? <CatalogListPortal /> : <CatalogSkeletonPortal />;
};
