import React from 'react';
import styled from 'styled-components';
import { push } from 'connected-react-router';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { ROUTES } from 'store/constants/router';
import { addressFullSubmitAction } from 'store/actions/server';
import { noop } from 'tools/noop';
import { InputPortal } from 'components/Input/Input@portal';
import { ButtonPortal, ButtonViews } from 'components/Button/Button@portal';

const StyledFirstRow = styled.div`
    grid-area: first-row;
    display: grid;
    grid-column-gap: 32px;
    grid-template-columns: 1.56fr 1fr;
    color: #fff;
`;

const StyledSecondRow = styled.div`
    grid-area: second-row;
    display: grid;
    grid-column-gap: 32px;
    grid-template-columns: 1fr 1fr 1fr;
    color: #fff;
`;

const StyledThirdRow = styled.div`
    grid-area: third-row;
    display: grid;
    grid-column-gap: 32px;
    grid-template-columns: 5.74fr 1fr;
    color: #fff;
`;

const StyledFourthRow = styled.div`
    grid-area: fourth-row;
`;

const SubmitButton = styled(ButtonPortal)`
    flex-grow: 0;
`;

const StyledRoot = styled.div`
    display: grid;
    padding: 16px 112px 0;
    box-sizing: border-box;
    grid-row-gap: 32px;
    grid-column-gap: 32px;
    grid-template-areas: 'first-row' 'second-row' 'third-row' 'fourth-row';

    will-change: transform;
`;

export const AddressFullAllPortal: React.FC = () => {
    const dispatch = useDispatch();
    const {
        addressInputValue,
        phoneNumberInputValue,
        apartmentInputValue,
        entranceInputValue,
        floorInputValue,
        doorCodeInputValue,
        isSubmitButtonDisabled,
    } = useSelector(({ addressFullPage }) => addressFullPage);
    const handleSubmitClick = React.useCallback(() => {
        dispatch(addressFullSubmitAction());
    }, [dispatch]);

    return (
        <StyledRoot>
            <StyledFirstRow>
                <InputPortal
                    title="  "
                    onChange={noop} //         
                    onRootClick={() => dispatch(push(ROUTES.ADDRESS_SIMPLE))}
                    value={addressInputValue}
                />
                <InputPortal
                    title=" "
                    onChange={noop} //         
                    onRootClick={() => dispatch(push(ROUTES.ADDRESS_FULL_PHONE))}
                    value={phoneNumberInputValue}
                />
            </StyledFirstRow>
            <StyledSecondRow>
                <InputPortal
                    title=""
                    onChange={noop} //         
                    onRootClick={() => dispatch(push(ROUTES.ADDRESS_FULL_APARTMENT))}
                    value={apartmentInputValue}
                />
                <InputPortal
                    title=""
                    onChange={noop} //         
                    onRootClick={() => dispatch(push(ROUTES.ADDRESS_FULL_ENTRANCE))}
                    value={entranceInputValue}
                />
                <InputPortal
                    title=""
                    onChange={noop} //         
                    onRootClick={() => dispatch(push(ROUTES.ADDRESS_FULL_FLOOR))}
                    value={floorInputValue}
                />
            </StyledSecondRow>
            <StyledThirdRow>
                <InputPortal
                    title=", ,  "
                    onChange={noop} //         
                    onRootClick={() => dispatch(push(ROUTES.ADDRESS_FULL_DOORCODE))}
                    value={doorCodeInputValue}
                />
            </StyledThirdRow>
            <StyledFourthRow>
                <SubmitButton view={ButtonViews.primary} disabled={isSubmitButtonDisabled} onClick={handleSubmitClick}>
                    
                </SubmitButton>
            </StyledFourthRow>
        </StyledRoot>
    );
};
