import { StoreUser } from 'store/constants/user';
import { selectSuggestItemById } from 'store/selectors';
import { isBuildingSuggest, isStreetSuggest } from 'tools/twoGis/api/suggest';
import {
    SUGGEST_ITEM_HEIGHT,
    SUGGEST_ITEM_MARGIN,
    AddressSimpleSuggestItemView,
} from 'pages/AddressSimple/SuggestItem/AddressSimpleSuggestItem';

import { AppReducer, AppState, initialState } from '../../constants';

const prepareSuggestItems = (state: AppState) => {
    const {
        twoGisSuggestsData: { itemsMap },
        addressSimplePage: { inputValue },
    } = state;

    return Object.values(itemsMap).reduce<AddressSimpleSuggestItemView[]>((result, suggestItem) => {
        if (isBuildingSuggest(suggestItem)) {
            result.push({ name: suggestItem.address_name, id: suggestItem.id });
        }

        if (isStreetSuggest(suggestItem)) {
            const { name, id } = suggestItem;

            if (name !== inputValue) {
                result.push({ id, name });
            }
        }

        return result;
    }, []);
};

export const getHasValidAddress = (user: StoreUser) => {
    const { street, house, city, address, addressLocationLatitude, addressLocationLongitude } = user;

    return street && house && city && address && addressLocationLatitude && addressLocationLongitude;
};

const prepareIsSubmitButtonDisabled = (state: AppState) => !getHasValidAddress(state.userModel);

const prepareSuggestListContainerYShift = ({ addressSimplePage: { selectedSuggestItemIndex, suggests } }: AppState) => {
    return (
        (SUGGEST_ITEM_HEIGHT + SUGGEST_ITEM_MARGIN) *
        Math.min(suggests.length - 3, Math.max(0, selectedSuggestItemIndex - 1))
    );
};

export const addressSimplePageView: AppReducer = (state, action) => {
    const { addressSimplePage, userModel } = state;

    switch (action.type) {
        case 'address/CHANGE_INPUT_VALUE': {
            addressSimplePage.selectedSuggestItemIndex = initialState.addressSimplePage.selectedSuggestItemIndex;

            break;
        }

        case 'address/SELECT_SUGGEST_ITEM': {
            addressSimplePage.selectedSuggestItemIndex = action.payload;

            break;
        }

        case 'address/CLICK_SUGGEST_ITEM': {
            const suggestItem = selectSuggestItemById(state, action.payload);

            addressSimplePage.selectedSuggestItemIndex = initialState.addressSimplePage.selectedSuggestItemIndex;

            if (isBuildingSuggest(suggestItem)) {
                addressSimplePage.submitButtonFocusHash = Math.random();
            } else {
                addressSimplePage.inputFocusHash = Math.random();
            }

            break;
        }

        case 'ADDRESS_INPUT': {
            addressSimplePage.inputFocusHash = Math.random();

            break;
        }

        default:
            break;
    }

    addressSimplePage.inputValue = userModel.address;
    addressSimplePage.suggests = prepareSuggestItems(state);
    addressSimplePage.suggestListContainerYShift = prepareSuggestListContainerYShift(state);
    addressSimplePage.isSubmitButtonDisabled = prepareIsSubmitButtonDisabled(state);
};
