import { catalogCardGap, catalogCardWidth } from 'pages/Catalog/Catalog.const/common';
import { CatalogListItemViewProps } from 'pages/Catalog/ListItem/CatalogListItem';
import { AppState, AppReducer, initialState } from 'store/constants';
import { ROUTES } from 'store/constants/router';
import {
    canAddMore,
    getCountById,
    getDisabled,
    getOldPriceById,
    preparePriceById,
    getQuantityLeftById,
    formatSale,
    wantMoreThanHave,
} from 'store/reducers/helpers/catalogPage';
import { selectProductById, selectPathname, selectProductDiffById } from 'store/selectors';
import { layoutWidth } from 'style/const/layout';
import { isPortal } from 'tools/browser';
import { memoize } from 'tools/memoize';

const screenItemCount = Math.floor(layoutWidth / (catalogCardWidth + catalogCardGap));

const listOffset = (-1 * (layoutWidth % (catalogCardWidth + catalogCardGap))) / 2;

const itemWidth = catalogCardWidth + catalogCardGap;

const getListIndexOffset = ({ catalogPage: { selectedItemId, order, listIndexOffset } }: AppState) => {
    if (isPortal) {
        return listIndexOffset;
    }

    const selectedItemIndex = order.indexOf(selectedItemId);

    if (selectedItemIndex === -1) {
        return 0;
    }

    const scrollPageSize = 1;

    if (selectedItemIndex <= listIndexOffset) {
        return Math.max(0, listIndexOffset - scrollPageSize);
    }

    if (selectedItemIndex >= listIndexOffset + screenItemCount - 1) {
        return Math.min(listIndexOffset + scrollPageSize, order.length - screenItemCount);
    }

    return listIndexOffset;
};

const getWrapperXShift = ({ catalogPage: { listIndexOffset } }: AppState) =>
    listOffset + listIndexOffset * itemWidth - catalogCardGap / 2;

const getScreenIndexMap = ({ catalogPage: { listIndexOffset, order } }: AppState): Record<string, number> =>
    new Array(screenItemCount).fill(0).reduce(
        (result, _, index) => ({
            ...result,
            [order[index + listIndexOffset]]: index + 1,
        }),
        {} as Record<string, number>,
    );

const prepareBadgeType = (state: AppState, itemId: string): CatalogListItemViewProps['badgeType'] => {
    const quantityLeft = getQuantityLeftById(state, itemId);
    const count = getCountById(state, itemId);
    const productItemDiff = selectProductDiffById(state, itemId);
    const productItem = selectProductById(state, itemId);

    // 
    if (quantityLeft === 0) {
        return 'sold-out';
    }

    //   
    if (count === quantityLeft) {
        return 'few-left';
    }

    //  
    if (productItemDiff && typeof productItemDiff.price !== 'undefined') {
        // 
        if (productItemDiff.price > productItem.price) {
            return 'price-changed';
        }

        // 
        if (productItemDiff.price < productItem.price) {
            return 'sale';
        }
    }

    return undefined;
};

const prepareBadgeContent = (state: AppState, itemId: string): CatalogListItemViewProps['badgeContent'] => {
    const quantityLeft = getQuantityLeftById(state, itemId);
    const count = getCountById(state, itemId);
    const productItemDiff = selectProductDiffById(state, itemId);
    const productItem = selectProductById(state, itemId);

    // 
    if (quantityLeft === 0) {
        return undefined;
    }

    //   
    if (count === quantityLeft) {
        return undefined;
    }

    //  
    if (productItemDiff && typeof productItemDiff.price !== 'undefined') {
        // 
        if (productItemDiff.price > productItem.price) {
            return undefined;
        }

        // 
        if (productItemDiff.price < productItem.price) {
            return formatSale(productItem.price, productItemDiff.price);
        }
    }

    return undefined;
};

const prepareIndex = ({ catalogPage: { screenIndexMap } }: AppState, itemId: string) => screenIndexMap[itemId];

const prepareCatalogItemsMap = (state: AppState) => {
    const { productsData } = state;

    return Object.keys(productsData.itemsMap).reduce<AppState['catalogPage']['itemsMap']>((acc, itemId) => {
        const { name, imageUrl } = selectProductById(state, itemId);

        acc[itemId] = {
            imageUrl,
            name,
            price: preparePriceById(state, itemId),
            oldPrice: getOldPriceById(state, itemId),
            isWantMoreThanHave: wantMoreThanHave(state, itemId),
            count: getCountById(state, itemId),
            index: prepareIndex(state, itemId),
            badgeType: prepareBadgeType(state, itemId),
            badgeContent: prepareBadgeContent(state, itemId),
            isButtonDisabled: !canAddMore(state, itemId),
            disabled: getDisabled(state, itemId),
        };

        return acc;
    }, {});
};

const prepareItemsFocusHashMap = (state: AppState) => {
    const {
        catalogPage: { itemsFocusHashMap, order },
    } = state;

    const isEmpty = Object.keys(itemsFocusHashMap).length === 0;
    const [firstItem] = order;

    return isEmpty && firstItem ? { [firstItem]: Math.random() } : itemsFocusHashMap;
};

const memoizedPrepareOrder = memoize((itemsMap: AppState['productsData']['itemsMap']) => {
    return Object.keys(itemsMap);
});

//    ,       ,
// (     )  
const prepareOrder = (state: AppState) => memoizedPrepareOrder(state.productsData.itemsMap);

export const catalogPageView: AppReducer = (state, action) => {
    const { catalogPage } = state;

    switch (action.type) {
        case 'ADD_ITEM': {
            if (selectPathname(state) === ROUTES.CATALOG) {
                catalogPage.selectedItemId = action.payload.item;
                catalogPage.itemsFocusHashMap[action.payload.item] = Math.random();
            }

            break;
        }

        case 'REMOVE_ITEM': {
            if (selectPathname(state) === ROUTES.CATALOG) {
                catalogPage.selectedItemId = action.payload.item;
                catalogPage.itemsFocusHashMap[action.payload.item] = Math.random();
            }

            break;
        }

        case 'REMOVE_ALL': {
            catalogPage.selectedItemId = initialState.catalogPage.selectedItemId;
            catalogPage.itemsFocusHashMap = initialState.catalogPage.itemsFocusHashMap;

            break;
        }

        case 'catalog/SELECT_ITEM': {
            catalogPage.selectedItemId = action.payload;

            break;
        }

        case 'catalog/SLIDE_LIST': {
            catalogPage.listIndexOffset = action.payload;
            catalogPage.selectedItemId = catalogPage.order[action.payload + 1];

            break;
        }

        default:
            break;
    }

    catalogPage.order = prepareOrder(state);
    catalogPage.listIndexOffset = getListIndexOffset(state);
    catalogPage.wrapperXShift = getWrapperXShift(state);
    catalogPage.screenIndexMap = getScreenIndexMap(state);
    catalogPage.itemsMap = prepareCatalogItemsMap(state);
    catalogPage.itemsFocusHashMap = prepareItemsFocusHashMap(state);
};
