import React from 'react';
import styled from 'styled-components';
import { color } from 'style/const/color';
import { BodyText2 } from 'components/Text/Body';

export enum ButtonViews {
    primary = 'primary',
    secondary = 'secondary',
}

const buttonViewBackgroundMap: Record<ButtonViews, string> = {
    primary: color.button.primary,
    secondary: color.button.secondary1,
};

const buttonBorderRadiusPx = 24;

interface StyledButtonProps {
    view: ButtonViews;
}
const StyledButton = styled(BodyText2)<StyledButtonProps>`
    position: relative;

    padding: 20px 40px;
    border-radius: ${buttonBorderRadiusPx}px;
    border: none;
    white-space: nowrap;

    display: flex;
    align-items: center;
    justify-content: center;

    transition: background-color 0.3s ease-in-out;

    background-color: ${({ view }) => buttonViewBackgroundMap[view]};
    color: ${color.text.primary};

    &[disabled] {
        opacity: 0.5;
    }
`;

export interface ButtonProps {
    className?: string;
    forwardRef?: React.RefObject<HTMLButtonElement>;
    view: ButtonViews;
    disabled?: boolean;
    onClick?: (event: React.SyntheticEvent<HTMLButtonElement>) => void;
}

/**
 * TODO:   
 * @see https://sbtatlas.sigma.sbrf.ru/jira/browse/FOODTECHUI-195
 */
export const ButtonPortal: React.FC<ButtonProps> = ({ forwardRef, ...props }) => {
    return <StyledButton as="button" type="button" ref={forwardRef} {...props} />;
};
