import { HeaderPortal } from 'components/Header/Header@portal';
import React from 'react';
import { layoutContentHeight, layoutHeaderPaddingTop } from 'style/const/layout@portal';
import styled from 'styled-components';
import { color } from 'style/const/color';
import gradientBackground from 'assets/images/background-tv-sber.jpg';
import { preventAllScroll } from 'tools/preventAllScroll';

const StyledContainer = styled.div`
    position: relative;

    width: 100%;
    height: 100%;

    padding-top: ${layoutHeaderPaddingTop}px;
    box-sizing: border-box;

    overflow: hidden;

    background-color: ${color.background.default};
    background-image: url('${gradientBackground}');
    background-position: center;
    background-size: cover;
`;

const AppContent = styled.div`
    position: relative;
    z-index: 2;

    height: ${layoutContentHeight}px;
`;

const StyledHeader = styled(HeaderPortal)`
    z-index: 3;
`;

export const AppLayoutPortal: React.FC = ({ children }) => {
    return (
        <StyledContainer onScroll={preventAllScroll}>
            <StyledHeader />
            <AppContent>{children}</AppContent>
        </StyledContainer>
    );
};
