import React from 'react';
import styled from 'styled-components';
import { Skeleton } from 'components/Skeleton/Skeleton';
import { color } from 'style/const/color';
import { layoutContentHeight, layoutHeaderPaddingHorizontal } from 'style/const/layout';
import {
    catalogCardBorderRadius,
    fullCardsInViewPort,
    catalogCardGap,
    catalogCardHeight,
    catalogCardWidth,
    catalogContentPaddingY,
} from 'pages/Catalog/Catalog.const/common';

const columnCount = fullCardsInViewPort + 1;

const skeletonWidthPx = catalogCardWidth * columnCount + catalogCardGap * fullCardsInViewPort;

const backgroundColor = color.surface.liquid1;
const foregroundColor = color.surface.liquid2;

const StyledRoot = styled.div`
    box-sizing: border-box;
    height: ${layoutContentHeight}px;
    padding-left: ${layoutHeaderPaddingHorizontal}px;
    padding-top: ${catalogContentPaddingY}px;
`;

export const CatalogSkeleton: React.FC = () => (
    <StyledRoot>
        <Skeleton
            width={skeletonWidthPx}
            height={catalogCardHeight}
            backgroundColor={backgroundColor}
            foregroundColor={foregroundColor}
            itemWidth={catalogCardWidth}
            itemHeight={catalogCardHeight}
            borderRadius={catalogCardBorderRadius}
            rowCount={1}
            columnCount={columnCount}
            paddingBetweenRows={catalogCardGap}
            paddingBetweenColumns={catalogCardGap}
            speed={2}
        />
    </StyledRoot>
);
