import React from 'react';
import styled from 'styled-components';
import { Provider as ReduxProvider } from 'react-redux';
import { Switch, Route } from 'react-router';
import { ConnectedRouter } from 'connected-react-router';
import { store, history } from 'store';
import { ROUTES } from 'store/constants/router';
import { AppLayoutPortal } from 'components/AppLayout/AppLayout@portal';
import { CatalogPortal } from 'pages/Catalog/Catalog@portal';
import { CartPortal } from 'pages/Cart/Cart@portal';
import { StatusPortal } from 'pages/Status/Status@portal';
import { PromoPortal } from 'pages/Promo/Promo@portal';
import { NoAreaPortal } from 'pages/NoArea/NoArea@portal';
import { AddressSimplePortal } from 'pages/AddressSimple/AddressSimple@portal';
import { AddressFullAllPortal } from 'pages/AddressFullAll/AddressFullAll@portal';
import { AddressFullApartmentPortal } from 'pages/AddressFullApartment/AddressFullApartment@portal';
import { AddressFullDoorCodePortal } from 'pages/AddressFullDoorCode/AddressFullDoorCode@portal';
import { AddressFullEntrancePortal } from 'pages/AddressFullEntrance/AddressFullEntrance@portal';
import { AddressFullFloorPortal } from 'pages/AddressFullFloor/AddressFullFloor@portal';
import { AddressFullPhonePortal } from 'pages/AddressFullPhone/AddressFullPhone@portal';
import { ErrorPortal } from 'pages/Error/Error@portal';
import { WaitPortal } from 'pages/Wait/Wait@portal';
import { preventAllScroll } from 'tools/preventAllScroll';

import 'style/global.scss';

const StyledContainer = styled.div`
    overflow: hidden;
    width: 100%;
    height: 100vh;
    outline: none;
    font-family: 'SB Sans Text', Helvetica, Arial, sans-serif;
    background: #000;
`;

export const RootPortal: React.FC = () => (
    <ReduxProvider store={store}>
        <ConnectedRouter history={history}>
            <StyledContainer onScroll={preventAllScroll}>
                <AppLayoutPortal>
                    <Switch>
                        <Route path={ROUTES.CATALOG}>
                            <CatalogPortal />
                        </Route>
                        <Route path={ROUTES.CART}>
                            <CartPortal />
                        </Route>
                        <Route path={ROUTES.STATUS}>
                            <StatusPortal />
                        </Route>
                        <Route path={ROUTES.ADDRESS_SIMPLE}>
                            <AddressSimplePortal />
                        </Route>
                        <Route path={ROUTES.ADDRESS_FULL_ALL}>
                            <AddressFullAllPortal />
                        </Route>
                        <Route path={ROUTES.ADDRESS_FULL_PHONE}>
                            <AddressFullPhonePortal />
                        </Route>
                        <Route path={ROUTES.ADDRESS_FULL_APARTMENT}>
                            <AddressFullApartmentPortal />
                        </Route>
                        <Route path={ROUTES.ADDRESS_FULL_ENTRANCE}>
                            <AddressFullEntrancePortal />
                        </Route>
                        <Route path={ROUTES.ADDRESS_FULL_FLOOR}>
                            <AddressFullFloorPortal />
                        </Route>
                        <Route path={ROUTES.ADDRESS_FULL_DOORCODE}>
                            <AddressFullDoorCodePortal />
                        </Route>
                        <Route path={ROUTES.PROMO}>
                            <PromoPortal />
                        </Route>
                        <Route path={ROUTES.NO_AREA}>
                            <NoAreaPortal />
                        </Route>
                        <Route path={ROUTES.ERROR}>
                            <ErrorPortal />
                        </Route>
                        <Route path={ROUTES.WAIT}>
                            <WaitPortal />
                        </Route>
                    </Switch>
                </AppLayoutPortal>
            </StyledContainer>
        </ConnectedRouter>
    </ReduxProvider>
);
