import { isMobile, isPortal } from 'tools/browser';

import { RootSberBox } from './Root@sberbox';
import { RootTouch } from './Root@touch';
import { RootPortal } from './Root@portal';

export const getRootForCurrentPlatform = () => {
    if (isMobile) {
        return RootTouch;
    }

    if (isPortal) {
        return RootPortal;
    }

    return RootSberBox;
};
