import React from 'react';
import styled from 'styled-components';
import { push } from 'connected-react-router';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { selectHeader } from 'store/selectors';
import { ROUTES } from 'store/constants/router';
import { HeaderButtonPortal } from 'components/HeaderButton/HeaderButton@portal';
import { icon } from 'style/const/icon';

import addressIcon from './address.svg';

const addressIconSizePx = icon.m.sizePx;

const StyledIcon = styled.div`
    width: ${addressIconSizePx}px;
    height: ${addressIconSizePx}px;

    background-image: url('${addressIcon}');
    background-repeat: no-repeat;
    background-size: contain;
`;

export const HeaderAddressPortal: React.FC = () => {
    const dispatch = useDispatch();

    const isCartEmpty = !useSelector(({ cart }) => cart.totalCount);
    const text = useSelector((state) => selectHeader(state).address.text);
    const isVisible = useSelector((state) => selectHeader(state).address.isVisible);

    const handleClick = React.useCallback(() => {
        dispatch(push(ROUTES.ADDRESS_SIMPLE));
    }, [dispatch]);

    const buttonText = isCartEmpty ? text : '';

    return (
        <HeaderButtonPortal isVisible={isVisible} onClick={handleClick} text={buttonText}>
            <StyledIcon />
        </HeaderButtonPortal>
    );
};
