import { goBack } from 'connected-react-router';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import React from 'react';
import { icon } from 'style/const/icon';
import styled from 'styled-components';

import backButtonIcon from './HeaderBack.assets/chevron-left.svg';

const backButtonIconSizePx = icon.m.sizePx;

interface StyledRootProps {
    isVisible?: boolean;
}
const StyledRoot = styled.span<StyledRootProps>`
    width: 80px;
    height: 80px;

    display: flex;
    align-items: center;
    justify-content: center;

    opacity: ${({ isVisible }) => (isVisible ? 1 : 0)};

    &::before {
        content: '';

        display: block;
        width: ${backButtonIconSizePx}px;
        height: ${backButtonIconSizePx}px;

        background-image: url(${backButtonIcon});
        background-repeat: no-repeat;
        background-size: contain;
    }
`;

interface HeaderBackPortalProps {
    className?: string;
}
export const HeaderBackPortal: React.FC<HeaderBackPortalProps> = ({ className }) => {
    const isVisible = useSelector(({ layout: { header } }) => header.isBackButtonVisible);
    const dispatch = useDispatch();

    const handleClick = React.useCallback(() => {
        if (isVisible) {
            dispatch(goBack());
        }
    }, [dispatch, isVisible]);

    return <StyledRoot className={className} tabIndex={-1} isVisible={isVisible} onClick={handleClick} />;
};
