import { FootnoteText1 } from 'components/Text/Footnote';
import React from 'react';
import styled from 'styled-components';
import { push } from 'connected-react-router';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { ROUTES } from 'store/constants/router';
import { makePurchaseAction } from 'store/actions/server';
import { ButtonPortal, ButtonViews } from 'components/Button/Button@portal';
import { Pin } from 'components/Pin/Pin';
import { ParagraphText2, ParagraphText1 } from 'components/Text/Paragraph';
import { CaptionText } from 'components/Text/Caption';
import { BodyText2 } from 'components/Text/Body';
import { color } from 'style/const/color';

const StyledCartInfo = styled.div`
    position: relative;

    height: 100%;
    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;
`;

const StyledTime = styled(ParagraphText2)`
    color: ${color.text.primary};
`;

const StyledUnit = styled(CaptionText)`
    margin-top: -10px;

    color: ${color.text.primary};
`;

const StyledAddress = styled(BodyText2)`
    margin-top: 32px;

    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;

    -webkit-line-clamp: 2;

    color: ${color.text.primary};
`;

const StyledPersonal = styled.div`
    margin-top: 8px;

    display: flex;
    flex-direction: column;
    align-items: center;
`;

const StyledPersonalLocation = styled(FootnoteText1)`
    -webkit-line-clamp: 2;
    text-align: center;

    color: ${color.text.secondary};
`;

const StyledPersonalPhoneNumber = styled(BodyText2)`
    margin-top: 32px;

    color: ${color.text.primary};
`;

const StyledPersonalName = styled(FootnoteText1)`
    margin-top: 8px;

    -webkit-line-clamp: 2;
    text-align: center;

    color: ${color.text.secondary};
`;

const StyledAskPersonal = styled(ParagraphText1)`
    margin-top: 8px;

    text-align: center;

    color: ${color.text.secondary};
`;

const CartInfoButton = styled(ButtonPortal)`
    margin-top: 48px;

    &[disabled] {
        opacity: 0.3;
    }
`;

export const CartInfoPortal: React.FC = () => {
    const dispatch = useDispatch();
    const { address, customerName } = useSelector(({ userData }) => userData);
    const {
        deliveryTime,
        isMakePurchaseButtonDisabled,
        makePurchaseButtonView,
        hasRequiredData,
        phoneNumber,
        personalAddress,
    } = useSelector(({ cartPagePortal }) => cartPagePortal);
    const handlePurchaseClick = React.useCallback(() => {
        dispatch(makePurchaseAction());
    }, [dispatch]);

    const handleSetAddressClick = React.useCallback(() => {
        dispatch(push(ROUTES.ADDRESS_FULL_ALL));
    }, [dispatch]);

    return (
        <StyledCartInfo>
            <Pin>
                <StyledTime>{deliveryTime}</StyledTime>
                <StyledUnit></StyledUnit>
            </Pin>
            <StyledAddress>{address}</StyledAddress>
            {hasRequiredData ? (
                <>
                    <StyledPersonal>
                        <StyledPersonalLocation>{personalAddress}</StyledPersonalLocation>
                        <StyledPersonalPhoneNumber>{phoneNumber}</StyledPersonalPhoneNumber>
                        <StyledPersonalName>{customerName}</StyledPersonalName>
                    </StyledPersonal>
                    <CartInfoButton
                        view={makePurchaseButtonView}
                        disabled={isMakePurchaseButtonDisabled}
                        onClick={handlePurchaseClick}
                    >
                          
                    </CartInfoButton>
                </>
            ) : (
                <>
                    <StyledAskPersonal>
                          ,  ,     
                    </StyledAskPersonal>
                    <CartInfoButton view={ButtonViews.primary} onClick={handleSetAddressClick}>
                         
                    </CartInfoButton>
                </>
            )}
        </StyledCartInfo>
    );
};
