import { GradientFade } from 'components/GradientFade/GradientFade';
import React from 'react';
import { setTwoGisSuggests } from 'store/actions/twoGisSuggests';
import styled from 'styled-components';
import Swiper from 'swiper';
import { useSelector } from 'hooks/useSelector';
import { useDispatch } from 'hooks/useDispatch';
import { getSuggestsList } from 'tools/twoGis/getSuggestsList';
import { addressChangeInputValueAction } from 'store/actions/addressSimplePage';
import { addressSubmitAction } from 'store/actions/server';
import { Input } from 'components/Input/Input';
import { ButtonPortal, ButtonViews } from 'components/Button/Button@portal';
import { preventAllScroll } from 'tools/preventAllScroll';

import { AddressSimpleSuggestListItemPortal } from './SuggestItem/AddressSimpleSuggestItem@portal';

const StyledContainer = styled.div`
    display: flex;
    align-items: start;
    height: 100%;
    padding: 16px 112px 0;
`;

const StyledInputContainer = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex: 1 0;
    max-width: 900px;
`;

const StyledButton = styled(ButtonPortal)`
    margin-left: 32px;
    padding: 36px 44px;
    width: 268px;
`;

interface StyledSuggestListProps {
    hidden?: boolean;
}
const StyledSuggestList = styled.div<StyledSuggestListProps>`
    height: ${({ hidden }) => (hidden ? 0 : 416)}px;
    width: 100%;
    overflow: hidden;
    transition: height 0.5s ease-in-out;
`;

const StyledSuggestListContainer = styled.div`
    display: flex;
    flex-direction: column;
`;

const StyledSwiperSlide = styled.div`
    height: 80px;
`;

const useKeyboardExpanded = () => {
    const [isKeyboardExpanded, setKeyboardExpanded] = React.useState(false);

    React.useEffect(() => {
        const onWindowResize = () => {
            setKeyboardExpanded(!isKeyboardExpanded);
        };

        window.addEventListener('resize', onWindowResize);

        return () => {
            window.removeEventListener('resize', onWindowResize);
        };
    }, [isKeyboardExpanded, setKeyboardExpanded]);

    return isKeyboardExpanded;
};

export const AddressSimplePortal: React.FC = () => {
    const [currentListIndex, setCurrentListIndex] = React.useState(0);
    const swiperRef = React.useRef<Swiper | null>(null);
    const ref = React.useRef<HTMLDivElement>(null);
    const dispatch = useDispatch();
    const inputValue = useSelector(({ addressSimplePagePortal }) => addressSimplePagePortal.inputValue);
    const suggests = useSelector(({ addressSimplePagePortal }) => addressSimplePagePortal.suggests);
    const isSubmitButtonDisabled = useSelector(
        ({ addressSimplePagePortal }) => addressSimplePagePortal.isSubmitButtonDisabled,
    );
    const latitude = useSelector(({ userData }) => userData.deviceLocationLatitude);
    const longitude = useSelector(({ userData }) => userData.deviceLocationLongitude);

    const isKeyboardExpanded = useKeyboardExpanded();

    const memoizedOnInputChange = React.useCallback<(e: React.ChangeEvent<HTMLInputElement>) => void>(
        (event) => {
            dispatch(addressChangeInputValueAction(event.currentTarget.value));
        },
        [dispatch],
    );
    const memoizedOnClickButton = React.useCallback(() => {
        dispatch(addressSubmitAction());
    }, [dispatch]);

    React.useEffect(() => {
        getSuggestsList({ q: inputValue, locale: 'ru_RU', latitude, longitude })
            .then((items) => {
                dispatch(setTwoGisSuggests(items));
            })
            .catch(() => {
                dispatch(setTwoGisSuggests([]));
            });
    }, [dispatch, inputValue, latitude, longitude]);

    React.useLayoutEffect(() => {
        if (ref.current instanceof HTMLDivElement) {
            swiperRef.current =
                swiperRef.current ||
                new Swiper(ref.current, {
                    direction: 'vertical',
                    slidesPerView: 'auto',
                    speed: 400,
                    initialSlide: 0,
                    spaceBetween: 24,
                    slidesOffsetBefore: 24,
                    on: {
                        slideChange: () => {
                            if (swiperRef.current) {
                                setCurrentListIndex(swiperRef.current.activeIndex);
                            }
                        },
                    },
                });
        }
    }, []);

    React.useEffect(() => {
        if (swiperRef.current) {
            swiperRef.current.updateSize();
            swiperRef.current.updateSlides();
            swiperRef.current.updateProgress();
            swiperRef.current.updateSlidesClasses();
            swiperRef.current.slideTo(0);
        }

        setCurrentListIndex(0);
    }, [suggests]);

    const suggestItems = React.useMemo(() => {
        return suggests.map((item, index) => (
            <StyledSwiperSlide key={item.id} className="swiper-slide">
                <AddressSimpleSuggestListItemPortal index={index} />
            </StyledSwiperSlide>
        ));
    }, [suggests]);

    const itemsInViewport = 4;
    const isTopGradientVisible = currentListIndex !== 0;
    // prettier-ignore
    const isBottomGradientVisible = (
        (currentListIndex === 0 && suggestItems.length > 3) ||
        currentListIndex < suggestItems.length - itemsInViewport
    );

    return (
        <StyledContainer>
            <StyledInputContainer>
                <Input value={inputValue} title="  " onChange={memoizedOnInputChange} />
                <GradientFade kind="top" sizePx={24} isVisible={isTopGradientVisible}>
                    <GradientFade kind="bottom" sizePx={24} isVisible={isBottomGradientVisible}>
                        <StyledSuggestList
                            ref={ref}
                            className="swiper-container"
                            hidden={isKeyboardExpanded}
                            onScroll={preventAllScroll}
                        >
                            <StyledSuggestListContainer className="swiper-wrapper">
                                {suggestItems}
                            </StyledSuggestListContainer>
                        </StyledSuggestList>
                    </GradientFade>
                </GradientFade>
            </StyledInputContainer>
            <StyledButton view={ButtonViews.primary} disabled={isSubmitButtonDisabled} onClick={memoizedOnClickButton}>
                
            </StyledButton>
        </StyledContainer>
    );
};
