import React from 'react';
import { layoutContentHeight } from 'style/const/layout';
import styled from 'styled-components';
import { useSelector } from 'hooks/useSelector';
import { preventAllScroll } from 'tools/preventAllScroll';
import { catalogCardGap, catalogCardHeight, catalogContentPaddingY } from 'pages/Catalog/Catalog.const/common';

import { CatalogListItem } from '../ListItem/CatalogListItem';

const StyledRoot = styled.div`
    position: relative;

    height: ${layoutContentHeight}px;
    padding-top: ${catalogContentPaddingY}px;
    box-sizing: border-box;

    display: flex;
    flex-direction: column;
    justify-content: start;

    overflow: hidden;
    --spatial-navigation-contain: contain;
`;

const StyledWrapper = styled.div`
    box-sizing: border-box;
    width: max-content;
    height: ${catalogCardHeight}px;
    min-width: 100%;

    display: grid;
    grid-template-columns: repeat(auto-fill, 392px);
    grid-auto-flow: row;
    grid-gap: ${catalogCardGap}px;

    will-change: transform;

    transition: transform 0.4s ease-in-out;
`;

export const CatalogList: React.FC = () => {
    const order = useSelector(({ catalogPage }) => catalogPage.order);
    const wrapperXShift = useSelector(({ catalogPage }) => catalogPage.wrapperXShift);

    const wrapperStyle = React.useMemo(
        () => ({
            transform: `translateX(${-1 * wrapperXShift}px)`,
        }),
        [wrapperXShift],
    );

    const items = React.useMemo(() => {
        return order.map((itemId) => <CatalogListItem key={itemId} id={itemId} />);
    }, [order]);

    return (
        <StyledRoot onScroll={preventAllScroll}>
            <StyledWrapper style={wrapperStyle}>{items}</StyledWrapper>
        </StyledRoot>
    );
};
