import React from 'react';
import { layoutHeaderHeight, layoutHeaderPaddingHorizontal } from 'style/const/layout@portal';
import styled from 'styled-components';
import { useSelector } from 'hooks/useSelector';
import { color } from 'style/const/color';
import { transition } from 'style/const/transition';
import { HeadlineText4 } from 'components/Text/Headline';
import { HeaderAddressPortal } from 'components/HeaderAddress/HeaderAddress@portal';
import { HeaderCartPortal } from 'components/HeaderCart/HeaderCart@portal';
import { HeaderBackPortal } from 'components/HeaderBack/HeaderBack@portal';

import logoIcon from './Header.assets/logo.svg';

interface StyledHeaderProps {
    isVisible: boolean;
}

const StyledHeader = styled.div<StyledHeaderProps>`
    position: relative;

    height: ${layoutHeaderHeight}px;

    box-sizing: border-box;
    padding: 0 80px 0 ${layoutHeaderPaddingHorizontal}px;

    display: flex;
    justify-content: space-between;
    align-items: center;

    --spatial-navigation-contain: contain;

    opacity: ${({ isVisible }) => Number(isVisible)};

    transition: opacity ${transition.default};
`;

const StyledSection = styled.div`
    display: flex;
    align-items: center;
`;

const logoSizePx = 72;

const StyledLogo = styled.div`
    width: ${logoSizePx}px;
    height: ${logoSizePx}px;
    border-radius: 16px;

    background-image: url(${logoIcon});
    background-repeat: no-repeat;
    background-size: contain;
`;

const StyledTitle = styled(HeadlineText4)`
    margin-left: 24px;

    display: inline-block;

    color: ${color.text.white};
`;

const StyledHeaderBack = styled(HeaderBackPortal)`
    position: absolute;
    top: 16px;
    left: 16px;
`;

interface HeaderPortalProps {
    className?: string;
}
export const HeaderPortal: React.FC<HeaderPortalProps> = ({ className }) => {
    //      ,   -
    //  ,        
    //  ,     
    const isVisible = useSelector(({ layout: { header } }) => header.isVisible);
    const title = useSelector(({ layout: { header } }) => header.title);

    return (
        <StyledHeader className={className} isVisible={isVisible}>
            <StyledHeaderBack />
            <StyledSection>
                <StyledLogo />
                <StyledTitle>{title}</StyledTitle>
            </StyledSection>
            <StyledSection>
                <HeaderAddressPortal />
                <HeaderCartPortal />
            </StyledSection>
        </StyledHeader>
    );
};
