import React from 'react';
import { push } from 'connected-react-router';
import styled from 'styled-components';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { selectHeader } from 'store/selectors';
import { ROUTES } from 'store/constants/router';
import { HeaderButtonPortal } from 'components/HeaderButton/HeaderButton@portal';
import { icon } from 'style/const/icon';
import { color } from 'style/const/color';

import cartIcon from './HeaderCart.assets/cart.svg';

const cartIconSizePx = icon.m.sizePx;

const StyledIcon = styled.div`
    position: relative;

    width: ${cartIconSizePx}px;
    height: ${cartIconSizePx}px;

    background-image: url('${cartIcon}');
    background-repeat: no-repeat;
    background-size: contain;
`;

const badgeSizePx = 32;
const badgeOffsetPx = cartIconSizePx / 2;

const StyledBadge = styled.span`
    position: absolute;
    left: ${badgeOffsetPx}px;
    bottom: ${badgeOffsetPx}px;

    width: ${badgeSizePx}px;
    height: ${badgeSizePx}px;
    border-radius: 50%;

    display: flex;
    justify-content: center;
    align-items: center;

    line-height: 24px;
    font-size: 20px;
    letter-spacing: -0.019em;

    box-sizing: border-box;

    color: ${color.text.primary};
    background: ${color.button.primary};
`;

export const HeaderCartPortal: React.FC = () => {
    const dispatch = useDispatch();

    const count = useSelector((state) => selectHeader(state).cart.count);
    const price = useSelector((state) => selectHeader(state).cart.price);
    const isVisible = useSelector((state) => selectHeader(state).cart.isVisible);

    const handleClick = React.useCallback(() => {
        dispatch(push(ROUTES.CART));
    }, [dispatch]);

    const text = `${price} `;

    return (
        <HeaderButtonPortal isVisible={isVisible} onClick={handleClick} text={text}>
            <StyledIcon>
                <StyledBadge>{count}</StyledBadge>
            </StyledIcon>
        </HeaderButtonPortal>
    );
};
