import React from 'react';
import { useSelector } from 'hooks/useSelector';

import { CatalogList } from './List/CatalogList';
import { CatalogSkeleton } from './Skeleton/CatalogSkeleton';

export const Catalog: React.FC = () => {
    const itemsCount = useSelector(({ catalogPage }) => catalogPage.order.length);

    return itemsCount ? <CatalogList /> : <CatalogSkeleton />;
};
