import React from 'react';
import styled from 'styled-components';
import { color } from 'style/const/color';

import shadowImage from './shadow.svg';

const StyledPin = styled.div`
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;

    height: 124px;
`;

const badgeHeightPx = 84;

interface StyledBodyProps {
    backgroundColor: string;
}
const StyledBody = styled.div<StyledBodyProps>`
    position: relative;

    height: ${badgeHeightPx}px;
    min-width: ${badgeHeightPx}px;
    padding: 10px 20px;
    border-radius: ${badgeHeightPx / 2}px;
    box-sizing: border-box;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    text-align: center;

    background: ${({ backgroundColor }) => backgroundColor};
`;

const kernelWidthPx = 4;
const kernelBorderRadiusPx = kernelWidthPx / 2;

interface StyledKernelProps {
    backgroundColor: string;
}
const StyledKernel = styled.div<StyledKernelProps>`
    height: 36px;
    width: ${kernelWidthPx}px;

    z-index: 1;

    border-bottom-left-radius: ${kernelBorderRadiusPx}px;
    border-bottom-right-radius: ${kernelBorderRadiusPx}px;

    background: ${({ backgroundColor }) => backgroundColor};
`;

const StyledShadow = styled.div`
    position: absolute;
    bottom: -6px;

    width: 86px;
    height: 18px;

    background-image: url('${shadowImage}');
`;

interface PinProps {
    backgroundColor?: string;
}
export const Pin: React.FC<PinProps> = ({ backgroundColor = color.button.critical, children }) => {
    return (
        <StyledPin>
            <StyledBody backgroundColor={backgroundColor}>{children}</StyledBody>
            <StyledKernel backgroundColor={backgroundColor} />
            <StyledShadow />
        </StyledPin>
    );
};
