import React from 'react';
import styled from 'styled-components';
import { goBack } from 'connected-react-router';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { closeAppAction } from 'store/actions/app';
import { ButtonPortal, ButtonViews } from 'components/Button/Button@portal';
import { HeadlineText2 } from 'components/Text/Headline';
import { BodyText1 } from 'components/Text/Body';
import { color } from 'style/const/color';
import { layoutContentHeight, layoutHeight } from 'style/const/layout@portal';

import circlesImage from './Error.assets/circles.svg';
import warningIcon from './Error.assets/warning.svg';

const ErrorPage = styled.div`
    position: relative;

    height: ${layoutContentHeight}px;
    display: flex;
    justify-content: start;
    align-items: center;
    padding-left: 112px;
`;

const TextContainer = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;

    align-items: start;
`;

const Title = styled(HeadlineText2)`
    width: 430px;
    color: ${color.text.primary};
`;

const Subtitle = styled(BodyText1)`
    margin-top: 24px;
    width: 430px;

    white-space: pre-wrap;

    color: ${color.text.secondary};
`;

const ButtonContainer = styled.div`
    margin-top: 48px;

    display: flex;
`;

const StyledButton = styled(ButtonPortal)`
    &:not(:first-child) {
        margin-left: 24px;
    }
`;

const circlesGroupSize = 1176;
const circlesGroupTopPosition = -1 * ((circlesGroupSize - layoutHeight) / 2);

const Circles = styled.div`
    position: fixed;
    top: ${circlesGroupTopPosition}px;
    bottom: 0;
    left: 324px;
    right: 0;

    box-sizing: border-box;
    width: ${circlesGroupSize}px;
    height: ${circlesGroupSize}px;

    display: flex;
    justify-content: center;
    align-items: center;

    background-image: url('${circlesImage}');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
`;

const WarningIcon = styled.div`
    width: 280px;
    height: 280px;

    background-image: url('${warningIcon}');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
`;

export const ErrorPortal: React.FC = () => {
    const dispatch = useDispatch();
    const { title, subtitle, retryButtonText, closeButtonText } = useSelector(({ errorPage }) => errorPage);

    const handleRetryButtonClick = React.useCallback(() => {
        dispatch(goBack());
    }, [dispatch]);

    const handleCloseButtonClick = React.useCallback(() => {
        dispatch(closeAppAction());
    }, [dispatch]);

    return (
        <ErrorPage>
            <TextContainer>
                <Title>{title}</Title>
                <Subtitle>{subtitle}</Subtitle>
                <ButtonContainer>
                    <StyledButton view={ButtonViews.secondary} onClick={handleRetryButtonClick}>
                        {retryButtonText}
                    </StyledButton>
                    {closeButtonText ? (
                        <StyledButton view={ButtonViews.secondary} onClick={handleCloseButtonClick}>
                            {closeButtonText}
                        </StyledButton>
                    ) : null}
                </ButtonContainer>
            </TextContainer>
            <Circles>
                <WarningIcon />
            </Circles>
        </ErrorPage>
    );
};
