interface DGBaseResponse {
    meta: {
        code: number;
        api_version: string;
        issue_date: string;
    };
}

export interface DGErrorResponse extends DGBaseResponse {
    meta: {
        code: number;
        api_version: string;
        issue_date: string;
        error: {
            type: string;
            message: string;
        };
    };
}

export interface DGSuccessResponse<ResponseType> extends DGBaseResponse {
    meta: {
        code: number;
        api_version: string;
        issue_date: string;
    };
    result: ResponseType;
}

export type DGApiResponse<ResponseType> = DGSuccessResponse<ResponseType> | DGErrorResponse;

export const isErrorResponse = (response: DGApiResponse<unknown>): response is DGErrorResponse =>
    'error' in response.meta;

export interface DGListResult<Item> {
    total: number;
    items: Item[];
}

export interface DGLatLng {
    lat: number;
    lon: number;
}
