import React from 'react';
import styled from 'styled-components';
import Swiper from 'swiper';
import { cartSlideListAction } from 'store/actions/cartPage';
import { useSelector } from 'hooks/useSelector';
import { useDispatch } from 'hooks/useDispatch';
import { FootnoteText1 } from 'components/Text/Footnote';
import { HeadlineText3 } from 'components/Text/Headline';
import { GradientFade } from 'components/GradientFade/GradientFade';
import { color } from 'style/const/color';
import { preventAllScroll } from 'tools/preventAllScroll';
import 'swiper/swiper-bundle.css';

import { CartListItemPortal } from '../ListItem/CartListItem@portal';
import {
    cartContentHeight,
    cartListGradientHeight,
    cartListItemHeight,
    cartListItemsInViewPort,
    cartListItemsPaddingBetween,
    cartListPaddingAfter,
    cartListPaddingBefore,
} from '../Cart.const/portal';

const StyledCartList = styled.div`
    position: relative;

    height: ${cartContentHeight}px;
    width: 100%;
    box-sizing: border-box;

    display: flex;
    flex-direction: column;
    justify-content: center;
`;

interface StyledContainerProps {
    topGradientVisible?: boolean;
    bottomGradientVisible?: boolean;
}
const StyledContainer = styled.div<StyledContainerProps>`
    position: relative;

    max-height: ${cartContentHeight}px;
    overflow: hidden;
`;

const StyledWrapper = styled.div``;

const StyledTotalPrice = styled(HeadlineText3)`
    padding-left: 16px;

    color: ${color.text.primary};
`;

const StyledMinimalPrice = styled(FootnoteText1)`
    margin-top: 8px;

    padding-left: 16px;

    color: ${color.text.warning};
`;

const StyledSwiperSlide = styled.div`
    height: ${cartListItemHeight}px;

    // swiper -        
    //      .   ,   .
    transform: none !important;
    display: flex;
    justify-content: center;
    align-items: center;
`;

const StyledPriceContainer = styled.div`
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
`;

export const CartListPortal: React.FC = () => {
    const dispatch = useDispatch();
    const ref = React.useRef<HTMLDivElement>(null);
    const swiperRef = React.useRef<Swiper | null>(null);
    const order = useSelector(({ cartPagePortal }) => cartPagePortal.order);
    const listIndexOffset = useSelector(({ cartPagePortal }) => cartPagePortal.listIndexOffset);
    const totalPrice = useSelector(({ cartPagePortal }) => cartPagePortal.totalPrice);
    const toMinimumPrice = useSelector(({ cartPagePortal }) => cartPagePortal.toMinimumPrice);
    const topGradientVisible = useSelector(({ cartPagePortal }) => cartPagePortal.topGradientVisible);
    const bottomGradientVisible = useSelector(({ cartPagePortal }) => cartPagePortal.bottomGradientVisible);

    /**
     *     useLayoutEffect - , 
     *          .
     * ,      ,   
     *         .
     */
    React.useLayoutEffect(() => {
        if (ref.current instanceof HTMLDivElement) {
            swiperRef.current =
                swiperRef.current ||
                new Swiper('.swiper-container', {
                    direction: 'vertical',
                    slidesPerView: 'auto',
                    centeredSlides: true,
                    centeredSlidesBounds: true,
                    /**
                     *        
                     *     .
                     */
                    normalizeSlideIndex: false,
                    speed: 400,
                    spaceBetween: cartListItemsPaddingBetween,
                    initialSlide: listIndexOffset,
                    roundLengths: true,
                    slidesOffsetBefore: cartListPaddingBefore,
                    slidesOffsetAfter: cartListPaddingAfter,
                    on: {
                        slideChange: () => {
                            if (swiperRef.current) {
                                //    swiper'
                                dispatch(cartSlideListAction(swiperRef.current.activeIndex));
                            }
                        },
                    },
                });
        }
    }, [dispatch, listIndexOffset]);

    /**
     *  swiper  .      
     * useLayoutEffect.       
     *    DOM (    
     *     ),
     *      .
     */
    React.useLayoutEffect(() => {
        if (swiperRef.current) {
            swiperRef.current.slideTo(listIndexOffset, 400);
        }
    }, [listIndexOffset]);

    /**
     *       .
     *     ,     
     *    .     .
     */
    React.useEffect(() => {
        const onKeyDown = (event: KeyboardEvent) => {
            if (event.key === 'ArrowUp') {
                const nextIndex = Math.max(0, listIndexOffset - cartListItemsInViewPort);

                dispatch(cartSlideListAction(nextIndex));
            }

            if (event.key === 'ArrowDown') {
                const maxIndex = order.length - 1;
                const nextIndex = Math.min(maxIndex, listIndexOffset + cartListItemsInViewPort);

                dispatch(cartSlideListAction(nextIndex));
            }
        };

        window.addEventListener('keydown', onKeyDown);

        return () => {
            window.removeEventListener('keydown', onKeyDown);
        };
    }, [dispatch, order.length, listIndexOffset]);

    React.useEffect(() => {
        if (swiperRef.current) {
            swiperRef.current.updateSize();
            swiperRef.current.updateSlides();
            swiperRef.current.updateProgress();
            swiperRef.current.updateSlidesClasses();
        }
    }, [order.length]);

    const items = React.useMemo(() => {
        return order.map((itemId, index) => (
            <StyledSwiperSlide className="swiper-slide" key={itemId}>
                <CartListItemPortal index={index + 1} id={itemId} />
            </StyledSwiperSlide>
        ));
    }, [order]);

    const isNotEnoughToMinimumPrice = Boolean(toMinimumPrice);
    const bottomGradientYOffset = 42 * (isNotEnoughToMinimumPrice ? 2 : 1);

    return (
        <StyledCartList>
            <GradientFade sizePx={cartListGradientHeight} kind="top" isVisible={topGradientVisible}>
                <GradientFade
                    sizePx={cartListGradientHeight}
                    yOffset={bottomGradientYOffset}
                    kind="bottom"
                    isVisible={bottomGradientVisible}
                >
                    <StyledContainer ref={ref} className="swiper-container" onScroll={preventAllScroll}>
                        <StyledWrapper className="swiper-wrapper">{items}</StyledWrapper>
                    </StyledContainer>
                </GradientFade>
            </GradientFade>
            <StyledPriceContainer>
                <StyledTotalPrice>  {totalPrice} </StyledTotalPrice>
                {isNotEnoughToMinimumPrice && (
                    <StyledMinimalPrice>    {toMinimumPrice} </StyledMinimalPrice>
                )}
            </StyledPriceContainer>
        </StyledCartList>
    );
};
