import { ButtonViews } from 'components/Button/Button';
import { CartListItemViewProps } from 'pages/Cart/ListItem/CartListItem';
import {
    canAddMore,
    getCountById,
    getDisabled,
    getOldPriceById,
    preparePriceById,
    getQuantityLeftById,
    formatSale,
    wantMoreThanHave,
} from 'store/reducers/helpers/catalogPage';
import {
    getFormattedPersonalAddress,
    prepareHasRequiredData,
    prepareToMinimumPrice,
    prepareTotalPrice,
} from 'store/reducers/helpers/cartPage';
import { selectProductById, selectCartItemById, selectProductDiffById } from 'store/selectors';
import { AppReducer, AppState } from 'store/constants';
import { formatPhoneNumber } from 'tools/format-phone-number';

const prepareLabel = (state: AppState, itemId: string): CartListItemViewProps['label'] => {
    const quantityLeft = getQuantityLeftById(state, itemId);
    const productItem = selectProductById(state, itemId);
    const productItemDiff = selectProductDiffById(state, itemId);
    const cartItem = selectCartItemById(state, itemId);
    const isWantMoreThanHave = wantMoreThanHave(state, itemId);

    // 
    if (quantityLeft === 0) {
        return { type: 'sold-out' };
    }

    //   
    if (cartItem && cartItem.count === quantityLeft) {
        return { type: 'few-left' };
    }

    //    
    if (isWantMoreThanHave) {
        return {
            type: 'you-want',
            content: String(cartItem.count),
        };
    }

    //  
    if (productItemDiff && typeof productItemDiff.price !== 'undefined') {
        // 
        if (productItemDiff.price > productItem.price) {
            return { type: 'price-changed' };
        }

        // 
        if (productItemDiff.price < productItem.price) {
            const content = formatSale(productItem.price, productItemDiff.price);
            return { type: 'sale', content };
        }
    }

    return undefined;
};

const prepareItemsMap = (state: AppState) => {
    const { cartPagePortal } = state;

    return cartPagePortal.order.reduce<AppState['cartPagePortal']['itemsMap']>((result, itemId) => {
        const cartItem = selectCartItemById(state, itemId);
        const { imageUrl, name } = selectProductById(state, itemId);

        if (cartItem) {
            result[itemId] = {
                imageUrl,
                name,
                price: preparePriceById(state, itemId),
                oldPrice: getOldPriceById(state, itemId),
                count: getCountById(state, itemId),
                isWantMoreThanHave: wantMoreThanHave(state, itemId),
                disabled: getDisabled(state, itemId),
                label: prepareLabel(state, itemId),
                isMoreButtonDisabled: !canAddMore(state, itemId),
            };
        }

        return result;
    }, {});
};

const calculateMakePurchaseDisabled = (state: AppState) => {
    const {
        cart: { totalPrice },
        delivery: { minimalPrice },
    } = state;

    return totalPrice < minimalPrice;
};

const calculateMakePurchaseButtonView = (state: AppState): AppState['cartPagePortal']['makePurchaseButtonView'] => {
    return state.cartPagePortal.isMakePurchaseButtonDisabled ? ButtonViews.secondary : ButtonViews.primary;
};

const prepareTopGradientVisible = ({ cartPagePortal: { listIndexOffset } }: AppState) => listIndexOffset > 0;

const prepareBottomGradientVisible = ({ cartPagePortal: { listIndexOffset, order } }: AppState) => {
    return (
        // prettier-ignore
        (listIndexOffset === 0 && order.length > 1) ||
        listIndexOffset < order.length - 1
    );
};

const prepareListIndexOffset = ({ cartPagePortal: { listIndexOffset, order } }: AppState) => {
    const maxValue = order.length - 1;
    const minValue = 0;

    //     
    return Math.max(minValue, Math.min(listIndexOffset, maxValue));
};

const getFormattedPhoneNumber = ({ userData }: AppState) =>
    userData.phoneNumber && formatPhoneNumber(userData.phoneNumber);

export const cartPagePortalView: AppReducer = (state, action) => {
    const { cart, delivery, cartPagePortal } = state;

    switch (action.type) {
        case 'cart/SLIDE_LIST': {
            cartPagePortal.listIndexOffset = action.payload;

            break;
        }

        default:
            break;
    }

    cartPagePortal.order = cart.order;
    cartPagePortal.deliveryTime = delivery.deliveryTime;
    cartPagePortal.itemsMap = prepareItemsMap(state);
    cartPagePortal.totalPrice = prepareTotalPrice(state);
    cartPagePortal.toMinimumPrice = prepareToMinimumPrice(state);
    cartPagePortal.isMakePurchaseButtonDisabled = calculateMakePurchaseDisabled(state);
    cartPagePortal.listIndexOffset = prepareListIndexOffset(state);

    cartPagePortal.makePurchaseButtonView = calculateMakePurchaseButtonView(state);
    cartPagePortal.topGradientVisible = prepareTopGradientVisible(state);
    cartPagePortal.bottomGradientVisible = prepareBottomGradientVisible(state);
    cartPagePortal.hasRequiredData = prepareHasRequiredData(state);
    cartPagePortal.phoneNumber = getFormattedPhoneNumber(state);
    cartPagePortal.personalAddress = getFormattedPersonalAddress(state);
};
