import { BodyText1 } from 'components/Text/Body';
import React from 'react';
import styled from 'styled-components';
import { useSelector } from 'hooks/useSelector';
import { useDispatch } from 'hooks/useDispatch';
import { addressClickSuggestItemAction } from 'store/actions/addressSimplePage';

const StyledRoot = styled(BodyText1)`
    padding: 20px 40px;
    box-sizing: border-box;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;

    border-radius: 24px;
    color: #fff;
    background: #1a1a1a;
`;

interface SuggestListItemPortalProps {
    index: number;
}

export interface AddressSimpleSuggestItemPortalView {
    id: string;
    name: string;
}

export const AddressSimpleSuggestListItemPortal: React.FC<SuggestListItemPortalProps> = ({ index }) => {
    const { name, id } = useSelector(({ addressSimplePagePortal: { suggests } }) => suggests[index]);
    const dispatch = useDispatch();
    const handleClick = React.useCallback(() => {
        dispatch(addressClickSuggestItemAction(id));
    }, [dispatch, id]);

    return <StyledRoot onClick={handleClick}>{name}</StyledRoot>;
};
